@import "utils";
@import "fonts";
@import "typography";

header {
  padding: 15px 25px;
  display: flex;
  justify-content: space-between;

  .header-logo {
    background: url(../images/logo.svg) no-repeat;
    width: 30px;
    height: 30px;

    a {
      padding: 15px;
    }
  }

  .header-nav {
    display: flex;
    justify-content: space-between;
    align-items: center;

    .header-ul {
      display: flex;
      justify-content: space-between;
      margin: 0;
      margin-right: 30px;

      li:not(:last-child) {
        margin-right: 30px;
      }
    }

    .header-profile {
      background: url(../images/profile-photo.svg) no-repeat;
      width: 30px;
      height: 30px;
      border-radius: 50%;

      a {
        padding: 15px;
      }
    }
  }
}

.registration-form {
  width: 330px;
  height: 320px;
  box-sizing: border-box;
  border: 1px solid #e1e7ed;
  border-radius: 3px;
  padding: 30px 30px 0;

  form {
    margin-top: 40px;

    input {
      width: 270px;
      height: 50px;
      border: 1px solid #e1e7ed;
      border-radius: 3px;
      margin-bottom: 16px;
      padding: 14px 17px;
      box-sizing: border-box;
      font-family: "Avenir", sans-serif;
      font-size: 16px;
      outline: none;
    }

    input[type="submit"] {
      margin-top: 8px;
      background: #fd5c63;
      border-radius: 4px;
      color: #fff;
    }
  }
}

footer {
  padding: 45px 25px;
  box-shadow: 0 -1px 0 #e1e7ed;

  .footer-wrapper {
    max-width: 900px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 20px;

    .footer-links {
      display: flex;
      flex-direction: column;

      .links-themes {
        font-weight: bold;
        margin-bottom: 20px;
      }

      a:not(:last-child) {
        margin-bottom: 10px;
      }
    }
  }
}
